// @flow

import React, { useMemo, useCallback, useState } from 'react';

import Arrow from '../../../assets/images/arrow.svg';

import styles from './index.module.scss';

interface Props{
  dispatch: (Object) => void,
  currentFilter?: { title: string } | null,
  filters: Array<{ title: string }>,
}

const ListFilter = ({ dispatch, currentFilter, filters }: Props) => {
  const [open, setOpen] = useState(false);

  const onArrowClick = useCallback(() => setOpen(!open), [open]);

  const onFilterClick = useCallback((filter: Object) => {
    if (currentFilter && (currentFilter.title === filter.title)) {
      dispatch(null);
    } else {
      dispatch(filter);
    }
    setOpen(false);
  }, [dispatch, currentFilter]);

  const filterListRender = useMemo(() => (
    filters.map((t) => {
      const isActive = currentFilter === t;
      return (
        <button key={t.title} type="button" onClick={() => onFilterClick(t)}>
          <p
            className={`${styles.filterItem} ${isActive ? styles.active : ''}`}
          >
            {t.title}
          </p>
        </button>
      );
    })
  ), [currentFilter, filters, onFilterClick]);

  const currentActiveFilter = useMemo(() => (
    <p className={styles.activeFilter}>{currentFilter ? currentFilter.title : 'Välj prisfilter'}</p>
  ), [currentFilter]);

  return (
    <div className={`${styles.listFilter} ${open ? styles.open : ''}`}>
      <div className={styles.container}>
        <div className={styles.listFilterOrigin}>
          <div className={styles.listFilterContainer}>
            {filterListRender}
          </div>
          <div className={styles.listFilterCover} />
        </div>
        {currentActiveFilter}
      </div>
      <button className={styles.toggleFilterBtn} type="button" onClick={onArrowClick}>
        <img src={Arrow} alt="toggle filter" />
      </button>
    </div>
  );
};

export default ListFilter;
