// @flow

import React, { useMemo } from 'react';
import { useParams } from 'react-router-dom';

import Stars from '../../components/stars';
import { SalonItem } from '../interfaces';

import OpenType from './open-type';
import DescrType from './descr-type';
import IconTextType from './icon-text-type';

import PinIcon from '../../assets/images/pin.svg';
import ClockIcon from '../../assets/images/clock.svg';
import GlobeIcon from '../../assets/images/globe.svg';
import PhoneIcon from '../../assets/images/phone.svg';

import Header from '../../components/header';

import styles from './index.module.scss';

const ComponentTypes = {
  adress: IconTextType,
  phone: IconTextType,
  open: OpenType,
  web: IconTextType,
  descr: DescrType,
};

const ImageTypes = {
  adress: PinIcon,
  phone: PhoneIcon,
  open: ClockIcon,
  web: GlobeIcon,
};

interface Props{
  salons: Array<SalonItem>,
}

const SingleView = ({ salons }: Props) => {
  const { salonId } = useParams();

  const salonData: SalonItem = useMemo(() => (
    salons.find((t) => t.id === salonId)
  ), [salons, salonId]);

  const infoRender = useMemo(() => {
    if (!salonData || !salonData.info) {
      return [];
    }

    const keys = Object.keys(salonData.info);
    return keys.map((t: string) => {
      const ComponentType = ComponentTypes[t];
      return (
        <div key={t} className={styles.infoItem}>
          <div className={styles.infoItemContainer}>
            {t !== 'descr' ? (
              <ComponentType type={t} icon={ImageTypes[t]} text={salonData.info[t]} />
            ) : (
              <DescrType text={salonData.info[t]} />
            )}
          </div>
        </div>
      );
    });
  }, [salonData]);

  return (
    <div className={styles.singleView}>
      <Header isSingleView />
      <div className={styles.topContainer}>
        <img src={salonData.cover} alt="salon hero" />
        <div className={styles.bottomGradient} />
        <div className={styles.bottomTopContainer}>
          <h2>{salonData.title}</h2>
          <Stars big amountStars={salonData.stars} amountReviews={salonData.reviews} />
        </div>
      </div>
      <div className={styles.navContainer}>
        <button type="button" className={styles.active}>
          <h4>Info</h4>
        </button>
        <button type="button">
          <h4>Schema</h4>
        </button>
      </div>
      <div className={styles.infoContainer}>
        {infoRender}
      </div>
    </div>
  );
};

export default SingleView;
