// @flow

import React, { useMemo, useState } from 'react';

import ListItem from './list-item';
import { SalonItem } from '../interfaces';
import Header from '../../components/header';
import ListFilter from './list-filter';

import styles from './index.module.scss';

const filters = [
  {
    title: 'Pris 250 - 500kr',
    min: 250,
    max: 500,
  },
  {
    title: 'Pris 500 - 750kr',
    min: 500,
    max: 750,
  },
];

interface Props{
  salons: Array<SalonItem>
}

const ListView = ({ salons }: Props) => {
  const [currentFilter, setCurrentFilter] = useState(null);
  const listItems = useMemo(() => (
    salons
      .filter((t: SalonItem) => {
        if (currentFilter) {
          return t.price >= currentFilter.min && t.price <= currentFilter.max;
        }
        return true;
      })
      .map((t: SalonItem) => (
        <ListItem key={t.id} salon={t} />
      ))
  ), [currentFilter, salons]);

  return (
    <div className={styles.listView}>
      <Header title="Hår" />
      <ListFilter dispatch={setCurrentFilter} currentFilter={currentFilter} filters={filters} />
      <div className={styles.listContainer}>
        {listItems}
      </div>
    </div>
  );
};

export default ListView;
