// @flow

import React, { useMemo } from 'react';

import EmptyStar from '../../assets/images/empty-star.svg';
import Star from '../../assets/images/star.svg';

import styles from './index.module.scss';

interface Props{
  amountStars: number,
  amountReviews: number,
  big?: boolean,
}

const Stars = ({ amountStars, amountReviews, big }: Props) => {
  const renderStars = useMemo(() => (
    Array.from({ length: 5 }, (t, i) => {
      const isStar = i < amountStars;
      return (
        <React.Fragment key={`${i}-star`}>
          {isStar ? (
            <img src={Star} alt="star" />
          ) : (
            <img src={EmptyStar} alt="empty star" />
          )}
        </React.Fragment>
      );
    })
  ), [amountStars]);

  return (
    <div className={`${styles.stars} ${big ? styles.large : ''}`}>
      <div className={styles.starsList}>
        {renderStars}
      </div>
      {big ? (
        <h5>{`(${amountReviews})`}</h5>
      ) : (
        <p>{`(${amountReviews})`}</p>
      )}
    </div>
  );
};

export default Stars;
