// @flow

import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from 'react-router-dom';

import ListView from './list-view';
import SingleView from './single-view';

import data from './data';

const App = () => (
  <Router>
    <Switch>
      <Route path="/:salonId">
        <SingleView salons={data} />
      </Route>
      <Route path="/" exact>
        <ListView salons={data} />
      </Route>
      <Redirect from="*" to="/" />
    </Switch>
  </Router>
);

export default App;
