// @flow

import React, { useMemo } from 'react';
import { useHistory } from 'react-router-dom';

import GoldenArrow from '../../assets/images/arrow.svg';
import WhiteArrow from '../../assets/images/white-arrow.svg';
import Heart from '../../assets/images/heart.svg';
import Filter from '../../assets/images/filter.svg';

import styles from './index.module.scss';

interface Props{
  isSingleView?: boolean,
  title?: string,
}

const Header = ({ isSingleView, title }: Props) => {
  const history = useHistory();
  const leftImg = useMemo(() => (
    <>
      {isSingleView ? (
        <button type="button" onClick={() => history.goBack()}>
          <img src={WhiteArrow} alt="back" />
        </button>
      ) : (
        <img className={styles.goldenArrow} src={GoldenArrow} alt="back" />
      )}
    </>
  ), [isSingleView, history]);

  const rightImg = useMemo(() => (
    <>
      {isSingleView ? (
        <img src={Heart} alt="heart" />
      ) : (
        <img src={Filter} alt="filter" />
      )}
    </>
  ), [isSingleView]);

  return (
    <div className={`${styles.header} ${isSingleView ? styles.singleView : ''}`}>
      {leftImg}
      {title && (
        <h2>{title}</h2>
      )}
      {rightImg}
    </div>
  );
};

export default Header;
