// @flow

import React from 'react';

import styles from '../index.module.scss';

interface Props{
  icon: string,
  text: string,
  type: string,
}

const IconTextType = ({ text, icon, type }: Props) => (
  <>
    <div className={styles.iconContainer}>
      <img src={icon} alt="adress icon" />
    </div>
    {type === 'phone' && (
      <a href={`tel:${text}`}>{text}</a>
    )}
    {type === 'web' && (
      <a target="_blank" rel="noopener noreferrer" href={`https://${text}`}>{text}</a>
    )}
    {type === 'adress' && (
      <p>{text}</p>
    )}
  </>
);

export default IconTextType;
